import React from 'react'
import './projects.css'
import { FaAndroid } from 'react-icons/fa'

export default function Projects(){

    return (
        <div className="container mx-auto py-16 flex items-center justify-center h-screen">
            <div className="relative h-80 w-3/12 bg-cover bg-center group rounded-lg overflow-hidden cvb">
                <div className="absolute inset-0 bg-black bg-opacity-50"></div>
                <div className="relative lg:px-4 h-full flex flex-col items-center justify-evenly">
                    <div className="text-white text-4xl flex font-bold">
                        Clinical Voice Box
                    </div>
                    <h3 className="text-white text-xl mt-2">
                        App creada para mejorar los procesos de evaluación y abordaje en el área de la voz.
                    </h3>
                    <h3 className="text-white text-lg mt-2">
                        +1000 Downloads
                    </h3>
                    <div>
                        <FaAndroid className="icon hover:text-green-600"/>
                    </div>
                </div>
            </div>
        </div>
    )
}